/* Coin.css */
body {
    overflow-x: auto;
    margin: 0;
  }
  
  .coin-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    padding: 10px;
    overflow-x: auto; /* Added for horizontal scroll */
  }
  
  .coin-row {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #d7d7d7;
    padding: 10px;
    min-width: 300px; /* Minimum width to prevent content from collapsing */
  }
  
  .coin {
    display: flex;
    align-items: center;
    padding-right: 10px;
    min-width: 200px; /* Adjusted minimum width for coin details */
  }
  
  .coin h1 {
    font-size: 14px; /* Reduced font size */
    margin: 0;
    padding: 0;
  }
  
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }
  
  .coin-symbol {
    text-transform: uppercase;
  }
  
  .coin-data {
    display: flex;
    flex-direction: column; /* Changed to column layout */
    align-items: center;
    text-align: center;
    width: 100%;
  }
  
  .coin-price,
  .coin-volume,
  .coin-marketcap,
  .coin-percent {
    width: 100%; /* Adjusted width to fit within column layout */
    font-size: 12px; /* Adjusted font size */
    margin: 2px 0;
  }
  
  .red {
    color: red;
  }
  
  .green {
    color: #11d811;
  }
  